body {
    margin: 0;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    margin-top: 20;
    padding-left: 20%;
    padding: 0;
    padding-top: 20;
    width: 100%;
    height: 3%;
    overflow: auto;
    background-color:darkcyan;
    text-align: center; 
  }
  
  li {
    display: inline-block;
    padding: 8px 16px;
    text-decoration: none; background-color:darkcyan; 
  }
  
  li a:hover:not(.active) {
    background-color:rgb(4, 109, 247);
    color: white;
  }

  h2 {
    float: left;
    text-align: center;

  }

  #styles{
    margin-left:1%;padding:50px 16px;height:50%;
  }

